import styled from "styled-components";
import { Container } from "../../gelobalStyles";
import { Link } from "react-router-dom";
// import { FaMagento } from "react-icons/fa"
import { GiEagleEmblem } from "react-icons/gi"



export const Nav = styled.nav`
    background: #101522;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
`
export const NavbarContainer = styled(Container)`
    display: flex;
    justify-content: space-between;
    height: 80px;

    ${Container}
`
export const NavLogo = styled(Link)`
    color: #fff;
    justify-self: flex-start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
`
export const NavIcon = styled(GiEagleEmblem)`
    margin-right: 0.5rem;
    font-size: 35px;
`

export const NavMobile = styled.div`
    display: none;

@media screen and (max-width: 960px){
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%,60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #fff;
}

`
export const NavMenu = styled.ul`
    display: flex;
    align-items: center;
    list-style: none;
    text-align: center;

    @media screen and (max-width:960px){
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 80px;
        left: ${({ click }) => (click ? 0 : "-100%")};
        opacity: 1;
        transition: 0.5s all ease;
        background: #101522;

    }
`

export const NanItem = styled.li`
    height: 80px;
    color: #fff;

    &:hover{
        border-bottom: solid 4px #4b59f7;
    }

    @media screen and (max-width:960px){
        width: 100%;
        color: #fff;

        &:hover{
            border: none;
        }
    }
`

export const NanLinks = styled(Link)`
    text-decoration: none;
    display: flex;
    align-items: center;
    padding: 0.5rem 1rem;
    height: 100%;
    color: #fff;
    /* justify-content: center; use text-align : center; */ 

    @media screen and (max-width:960px){
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
        font-size: 25   px;

        &:hover{
            color: #4b59f7;
            transition: 0.5s all ease;
            background: #fff;
        }
    }
`
export const NavItemBtn = styled.li`
    @media screen and (max-width:960px){
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 120px;
    }
`

export const NavBtnLink = styled(Link)`
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 8px 16px;
    height: 100%;
    width: 100%;
    border: none;
    outline: none;
`