import React, { useState, useEffect } from 'react'
import { FaBars, FaTimes, } from 'react-icons/fa';
import { Button } from '../../gelobalStyles';
import {
    Nav,
    NavbarContainer,
    NavLogo,
    NavIcon,
    NavMobile,
    NavMenu,
    NanItem,
    NanLinks,
    NavItemBtn,
    NavBtnLink
}
    from './Navbar.elements';

function Navbar() {

    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true);

    const hendelClick = () => {
        setClick(!click);
    }

    const handelButton = () => {
        if (window.innerWidth < 960) {
            setButton(false);
        } else {
            setButton(true);
        }
    }

    window.addEventListener("resize", handelButton);

    const hendelNavClick = () => {
        if (click === true) {
            hendelClick();
        }
    }

    useEffect(() => {
        handelButton();
    }, [])
    return (
        <>
            <Nav>
                <NavbarContainer onClick={hendelNavClick}>
                    <NavLogo to="/">
                        <NavIcon />
                        EAGLE
                    </NavLogo>
                    <NavMobile onClick={hendelClick}>
                        {click ? <FaTimes /> : <FaBars />}
                    </NavMobile>
                    <NavMenu onClick={hendelNavClick} click={click}>
                        <NanItem>
                            <NanLinks to="/">
                                Home
                            </NanLinks>
                        </NanItem>
                        <NanItem>
                            <NanLinks to="/services">
                                Services
                            </NanLinks>
                        </NanItem>
                        <NanItem>
                            <NanLinks to="/products">
                                Products
                            </NanLinks>
                        </NanItem>
                        <NavItemBtn>
                            {button ? (
                                <NavBtnLink to="/sign-up">
                                    <Button primary>SIGN UP</Button>
                                </NavBtnLink>
                            ) : (
                                <NavBtnLink to="/sign-up">
                                    <Button fontbig primary>SIGN UP</Button>
                                </NavBtnLink>
                            )}
                        </NavItemBtn>
                    </NavMenu>
                </NavbarContainer>
            </Nav>
        </>
    )
}

export default Navbar
