import React from 'react'
import { GiRock } from 'react-icons/gi'
import { Button } from '../../gelobalStyles'
import {
    PricingContainer,
    PricingWrapper,
    PricingTitle,
    PricingBox,
    PricingCard,
    PricingInfo,
    PricingCardIcon,
    PricingCardPlan,
    PricingCardCost,
    PricingCardLength,
    PricingCardFeatures,
    PricingCardFeature
} from './Pricing.elements'

function Pricing() {
    return (
        <>
            <PricingContainer>
                <PricingWrapper>
                    <PricingTitle>
                        Our Services
                    </PricingTitle>
                    <PricingBox>
                        <PricingCard to="sign-up">
                            <PricingInfo>
                                <PricingCardIcon>
                                <GiRock />
                                </PricingCardIcon>
                                <PricingCardPlan>Starter Pack</PricingCardPlan>
                                <PricingCardCost>$99.99</PricingCardCost>
                                <PricingCardLength>per month</PricingCardLength>
                                <PricingCardFeatures>
                                    <PricingCardFeature>100 New Users</PricingCardFeature>
                                    <PricingCardFeature>$10,000 Budget</PricingCardFeature>
                                    <PricingCardFeature>Retargeting analytics</PricingCardFeature>
                                </PricingCardFeatures>
                                <Button primary >Choose Plan</Button>
                            </PricingInfo>
                        </PricingCard>
                        <PricingCard to="sign-up">
                            <PricingInfo>
                                <PricingCardIcon>
                                <GiRock />
                                </PricingCardIcon>
                                <PricingCardPlan>Starter Pack</PricingCardPlan>
                                <PricingCardCost>$99.99</PricingCardCost>
                                <PricingCardLength>per month</PricingCardLength>
                                <PricingCardFeatures>
                                    <PricingCardFeature>100 New Users</PricingCardFeature>
                                    <PricingCardFeature>$10,000 Budget</PricingCardFeature>
                                    <PricingCardFeature>Retargeting analytics</PricingCardFeature>
                                </PricingCardFeatures>
                                <Button primary >Choose Plan</Button>
                            </PricingInfo>
                        </PricingCard>
                        <PricingCard to="sign-up">
                            <PricingInfo>
                                <PricingCardIcon>
                                <GiRock />
                                </PricingCardIcon>
                                <PricingCardPlan>Starter Pack</PricingCardPlan>
                                <PricingCardCost>$99.99</PricingCardCost>
                                <PricingCardLength>per month</PricingCardLength>
                                <PricingCardFeatures>
                                    <PricingCardFeature>100 New Users</PricingCardFeature>
                                    <PricingCardFeature>$10,000 Budget</PricingCardFeature>
                                    <PricingCardFeature>Retargeting analytics</PricingCardFeature>
                                </PricingCardFeatures>
                                <Button primary >Choose Plan</Button>
                            </PricingInfo>
                        </PricingCard>
                    </PricingBox>
                </PricingWrapper>
            </PricingContainer>
        </>
    )
}

export default Pricing
