import { Link } from "react-router-dom";
import styled from "styled-components";
import { GiEagleEmblem } from "react-icons/gi"

export const FooterContainer = styled.div`
    display: flex;
    padding: 4rem 0 2rem 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #101522;
`
export const FooterSubscription = styled.section`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 24px;
    padding: 24px;
    color: #fff;
`
export const FooterSubHeading = styled.p`
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
    margin-bottom: 24px;
    font-size: 24px;
`
export const FooterSubText = styled.p`
    margin-bottom: 24px;
    font-size: 24px;
`
export const Form = styled.form`
    display: flex;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 820px) {
        flex-direction: column;
        width: 80%;
    }

`
export const FormInput = styled.input`
    padding: 10px 20px;
    border-radius: 2px;
    margin-right: 10px;
    outline: none;
    font-size: 16px;
    border: solid 1px #fff;

    &::placeholder{
        color: #242424;
    }

    @media screen and (max-width:820px){
        width: 100%;
        margin: 0 0 16px 0;
    }

`

export const FooterLinksContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    max-width: 1000px;

    @media screen and (max-width:820px){
        padding-top: 32px;
    }
`
export const FooterLinksWrapper = styled.div`
    display: flex;

    @media screen and (max-width:820px){
        flex-direction: column;
    }
`

export const FooterLinksItmes = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    width: 160px;
    box-sizing: border-box;
    color: #fff;

    @media screen and (max-width:420px){
        width: 100%;
        margin: 0;
        padding: 10px;
    }
`
export const FooterLinkTitle = styled.h2`
    margin-bottom: 16px;
`
export const FooterLink = styled(Link)`
    color: #fff;
    text-decoration: none;
    margin-bottom: 0.5rem;
    border-bottom: solid  #101522 1px;
    padding-bottom: 3px;
    &:hover{
        color: #0467FB;
        transition: 0.3s all ease-out;
        border-bottom: solid  #0467FB 1px;
    }
`

export const SocialMedia = styled.section`
    max-width: 1000px;
    width: 100%;
`
export const SocialWrapper = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1000px;
    width: 90%;
    margin: 40px auto 0 auto;

    @media screen and (max-width: 820px) {
        flex-direction: column;
    }
`
export const SpcialLogo = styled(Link)`
    color: #fff;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
`
export const SocialIcon = styled(GiEagleEmblem)`
    margin-right: 10px;
`
export const WebsiteRight = styled.small`
    color: #fff;
    margin-bottom: 16px;
`
export const SocilaIcons = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
`
export const SocialIconLink = styled.a`
    color: #fff;
    text-decoration: none;
    font-size: 24px;
`