import React from 'react'
import { FaFacebook, FaInstagram, FaLinkedin, FaTwitter, FaYoutube } from 'react-icons/fa'
import { Button } from '../../gelobalStyles'
import {
    FooterContainer,
    FooterSubscription,
    FooterSubHeading,
    FooterSubText,
    Form,
    FormInput,
    FooterLinksContainer,
    FooterLinksWrapper,
    FooterLinksItmes,
    FooterLinkTitle,
    FooterLink,
    SocialMedia,
    SocialWrapper,
    SpcialLogo,
    SocialIcon,
    WebsiteRight,
    SocilaIcons,
    SocialIconLink
} from "./Footer.elements"

function Footer() {
    return (
        <>
            <FooterContainer>
                <FooterSubscription>
                    <FooterSubHeading>
                        Join our exclusive membership to
                        receive the latest news trends
                    </FooterSubHeading>
                    <FooterSubText>
                        You can unsubscribe at any time.
                    </FooterSubText>
                    <Form>
                        <FormInput name="email" type="email" placeholder="Your Email" />
                        <Button fontBig type="submit">Subscribe</Button>
                    </Form>
                </FooterSubscription>
                <FooterLinksContainer>
                    <FooterLinksWrapper>
                        <FooterLinksItmes>
                            <FooterLinkTitle>About Us</FooterLinkTitle>
                            <FooterLink to="/sign-up">How it works</FooterLink>
                            <FooterLink to="/">Testimonials</FooterLink>
                            <FooterLink to="/">Careers</FooterLink>
                            <FooterLink to="/">Investors</FooterLink>
                            <FooterLink to="/">Terms of Service</FooterLink>
                        </FooterLinksItmes>
                        <FooterLinksItmes>
                            <FooterLinkTitle>Contact Us</FooterLinkTitle>
                            <FooterLink to="/sign-up">How it works</FooterLink>
                            <FooterLink to="/">Testimonials</FooterLink>
                            <FooterLink to="/">Careers</FooterLink>
                            <FooterLink to="/">Investors</FooterLink>
                            <FooterLink to="/">Terms of Service</FooterLink>
                        </FooterLinksItmes>
                    </FooterLinksWrapper>
                    <FooterLinksWrapper>
                        <FooterLinksItmes>
                            <FooterLinkTitle>Videos</FooterLinkTitle>
                            <FooterLink to="/sign-up">How it works</FooterLink>
                            <FooterLink to="/">Testimonials</FooterLink>
                            <FooterLink to="/">Careers</FooterLink>
                            <FooterLink to="/">Investors</FooterLink>
                            <FooterLink to="/">Terms of Service</FooterLink>
                        </FooterLinksItmes>
                        <FooterLinksItmes>
                            <FooterLinkTitle>Social Media</FooterLinkTitle>
                            <FooterLink to="/sign-up">How it works</FooterLink>
                            <FooterLink to="/">Testimonials</FooterLink>
                            <FooterLink to="/">Careers</FooterLink>
                            <FooterLink to="/">Investors</FooterLink>
                            <FooterLink to="/">Terms of Service</FooterLink>
                        </FooterLinksItmes>
                    </FooterLinksWrapper>
                </FooterLinksContainer>
                <SocialMedia>
                    <SocialWrapper>
                        <SpcialLogo to="/">
                            <SocialIcon />
                            EAGLE
                        </SpcialLogo>
                        <WebsiteRight>EAGLE &copy; 2021, Desgined By Mohamed AraB</WebsiteRight>
                        <SocilaIcons>
                            <SocialIconLink href="/" target='_blank' aria-label="Facebook">
                                <FaFacebook />
                            </SocialIconLink>
                            <SocialIconLink href="/" target='_blank' aria-label="Instagram">
                                <FaInstagram />
                            </SocialIconLink>
                            <SocialIconLink href="/" target='_blank' aria-label="Youtube">
                                <FaYoutube />
                            </SocialIconLink>
                            <SocialIconLink href="/" target='_blank' aria-label="Twitter">
                                <FaTwitter />
                            </SocialIconLink>
                            <SocialIconLink href="/" target='_blank' aria-label="LinkedIn">
                                <FaLinkedin />
                            </SocialIconLink>
                        </SocilaIcons>
                    </SocialWrapper>
                </SocialMedia>
            </FooterContainer>
        </>
    )
}

export default Footer
